import {
	TableBody,
	TableContainer,
	Table,
	TableRow,
	TableCell,
	MenuItem,
	Select,
} from '@material-ui/core';
import { useFormikContext } from 'formik';
import _get from 'lodash.get';
import Header from './Header';
import {
	TJurisdictionalCost,
	TJurisdictionalCostTable,
	TPropertyFormDataStatus,
	TJurisdictionalCostItem,
} from '../../../../../types';
import { isDisabledFee, isFormListingSubmitted, getHoaItems } from './helper';
import config from '../../../../../config';
import Amount from './Component/Amount';

type Props = {
	jurisdictionalCost: TJurisdictionalCost;
	propertyFormDataStatuses?: Array<TPropertyFormDataStatus>;
};
export const HoaTable = ({
	jurisdictionalCost,
	propertyFormDataStatuses,
}: Props) => {
	const { values, handleChange, handleBlur } =
		useFormikContext<TJurisdictionalCostTable>();

	const isDisabled =
		propertyFormDataStatuses && isFormListingSubmitted(propertyFormDataStatuses);
	const payorOptions = _get(jurisdictionalCost, 'PayorOptions');
	const items = _get(
		jurisdictionalCost,
		'Items'
	) as Array<TJurisdictionalCostItem>;
	const filterItems = getHoaItems(items);

	return (
		<TableContainer>
			<Table aria-labelledby='tableTitle' size='small'>
				<Header />
				<TableBody>
					{filterItems.map(item => {
						if (typeof item === 'number') return null;
						const itemKey = item.ItemKey;
						return (
							<TableRow key={itemKey}>
								<TableCell align='left'>{item.ItemName}</TableCell>
								<TableCell align='left'>
									<Amount item={item} />
								</TableCell>
								<TableCell align='left'>
									{item.Payor || config.defaultText.noValue}
								</TableCell>
								<TableCell align='left'>
									<Select
										labelId={itemKey}
										id={itemKey}
										name={itemKey}
										value={values[`${itemKey}`]}
										onChange={handleChange}
										onBlur={handleBlur}
										displayEmpty
										disabled={isDisabledFee(item) || isDisabled}
									>
										<MenuItem value=''>Customary Terms</MenuItem>
										{payorOptions.map(({ Payor: payor, PayorId: payorId }) => (
											<MenuItem key={payorId} value={payorId}>
												{payor}
											</MenuItem>
										))}
									</Select>
								</TableCell>
							</TableRow>
						);
					})}
				</TableBody>
			</Table>
		</TableContainer>
	);
};

export default HoaTable;
