import * as yup from 'yup';

export const validationAddHoa = yup.object().shape({
	PrivateTransferFeeAmount: yup.string().ensure().when('hoaDetectedOverride', {
		is: 'Yes',
		then: yup.string().required(),
	}),
	PrivateTransferFeeAmountType: yup
		.string()
		.ensure()
		.when('hoaDetectedOverride', {
			is: 'Yes',
			then: yup.string().required(),
		}),
});
