import {
	TableBody,
	TableContainer,
	Table,
	TableRow,
	TableCell,
	MenuItem,
	Select, makeStyles
} from '@material-ui/core';
import { useFormikContext } from 'formik';
import _get from 'lodash.get';
import Header from './Header';
import {
	TJurisdictionalCost,
	TJurisdictionalCostTable,
	TPropertyFormDataStatus,
	TJurisdictionalCostItem,
} from '../../../../../types';
import {
	getPayAmount,
	isDisabledFee,
	isFormListingSubmitted,
	getGeneralItems,
} from './helper';

const useStyles = makeStyles({
	table: {
		marginBottom: '10px',
	},
});

type Props = {
	jurisdictionalCost: TJurisdictionalCost;
	propertyFormDataStatuses?: Array<TPropertyFormDataStatus>;
};

export const SettlementTermsTable = ({
	jurisdictionalCost,
	propertyFormDataStatuses,
}: Props) => {
	const classes = useStyles();
	const { values, handleChange, handleBlur } =
		useFormikContext<TJurisdictionalCostTable>();

	const isDisabled =
		propertyFormDataStatuses && isFormListingSubmitted(propertyFormDataStatuses);
	const payorOptions = _get(jurisdictionalCost, 'PayorOptions');
	const items = _get(
		jurisdictionalCost,
		'Items'
	) as Array<TJurisdictionalCostItem>;
	const filterItems = getGeneralItems(items);

	return (
		<TableContainer className={classes.table}>
			<Table aria-labelledby='tableTitle' size='small'>
				<Header />
				<TableBody>
					{filterItems.map(item => {
						if (typeof item === 'number') return null;
						const itemKey = item.ItemKey;
						return (
							<TableRow key={item.ItemKey}>
								<TableCell align='left'>{item.ItemName}</TableCell>
								<TableCell align='left'>{getPayAmount(item.PayAmount)}</TableCell>
								<TableCell align='left'>{item.Payor}</TableCell>
								<TableCell align='left'>
									<Select
										labelId={itemKey}
										id={itemKey}
										name={itemKey}
										value={values[`${itemKey}`]}
										onChange={handleChange}
										onBlur={handleBlur}
										displayEmpty
										disabled={isDisabledFee(item) || isDisabled}
									>
										<MenuItem value=''>Customary Terms</MenuItem>
										{payorOptions.map(({ Payor: payor, PayorId: payorId }) => (
											<MenuItem key={payorId} value={payorId}>
												{payor}
											</MenuItem>
										))}
									</Select>
								</TableCell>
							</TableRow>
						);
					})}
				</TableBody>
			</Table>
		</TableContainer>
	);
};

export default SettlementTermsTable;
