import { useFormikContext } from 'formik';
import _get from 'lodash.get';
import { MenuItem, Select, TextField, makeStyles } from '@material-ui/core';
import React from 'react';
import {
	TJurisdictionalCostTable,
	TJurisdictionalCostItem,
} from '../../../../../../types';
import { getPayAmount, AMOUNT_TYPES, PRIVATE_TRANSFER_FEE } from '../helper';

const useStyles = makeStyles(() => ({
	amount: {
		marginBottom: '10px',
	},
}));

type Props = {
	item: TJurisdictionalCostItem;
};
export const Amount = ({ item }: Props) => {
	const classes = useStyles();
	const { values, handleChange, handleBlur, touched, errors } =
		useFormikContext<TJurisdictionalCostTable>();
	const amount = _get(item, 'PayAmount') || '';
	const itemKey = _get(item, 'ItemKey') || '';

	if (itemKey !== PRIVATE_TRANSFER_FEE) return <>{getPayAmount(amount)}</>;

	const itemAmountKey = `${itemKey}Amount`;
	const itemAmountTypeKey = `${itemKey}AmountType`;

	return (
		<>
			<TextField
				defaultValue={values[`${itemAmountKey}`]}
				fullWidth
				placeholder='Amount'
				onChange={handleChange}
				onBlur={handleBlur}
				name={itemAmountKey}
				className={classes.amount}
				error={touched[`${itemAmountKey}`] && Boolean(errors[`${itemAmountKey}`])}
			/>
			<Select
				fullWidth
				labelId={itemAmountTypeKey}
				id={itemAmountTypeKey}
				name={itemAmountTypeKey}
				value={values[`${itemAmountTypeKey}`]}
				onChange={handleChange}
				onBlur={handleBlur}
				displayEmpty
				error={
					touched[`${itemAmountTypeKey}`] && Boolean(errors[`${itemAmountTypeKey}`])
				}
			>
				{AMOUNT_TYPES.map(type => (
					<MenuItem key={type} value={type}>
						{type}
					</MenuItem>
				))}
			</Select>
		</>
	);
};

export default Amount;
