import {
	MenuItem,
	Select,
	List,
	ListItemText,
	Typography,
	ListItem,
} from '@material-ui/core';
import { useFormikContext } from 'formik';
import _get from 'lodash.get';
import {
	TJurisdictionalCost,
	TJurisdictionalCostTable,
	TPropertyFormDataStatus,
} from '../../../../../types';
import { isYes } from '../../../../../helpers';
import HoaTable from './HoaTable';
import config from '../../../../../config';

type Props = {
	jurisdictionalCost: TJurisdictionalCost;
	propertyFormDataStatuses?: Array<TPropertyFormDataStatus>;
};
export const Hoa = ({
	jurisdictionalCost,
	propertyFormDataStatuses,
}: Props) => {
	const { values, handleChange } = useFormikContext<TJurisdictionalCostTable>();
	const isHoaDetected = Boolean(_get(jurisdictionalCost, 'HOADetected', false));

	const isDisplay = isYes(values.hoaDetectedOverride) || false;

	return (
		<>
			<List disablePadding>
				<ListItem>
					<ListItemText primary='Hoa Detected' />
					<Typography variant='body2'>{isHoaDetected ? 'Yes' : 'No'}</Typography>
				</ListItem>
				<ListItem>
					<ListItemText primary='Hoa Detected Override' />
					<Typography variant='body2'>
						<Select
							labelId='hoaDetectedOverride'
							id='hoaDetectedOverride'
							value={values.hoaDetectedOverride}
							label='hoaDetectedOverride'
							onChange={handleChange}
							name='hoaDetectedOverride'
							displayEmpty
						>
							<MenuItem value=''>{config.defaultText.noneValue}</MenuItem>
							<MenuItem value='No'>No</MenuItem>
							<MenuItem value='Yes'>Yes</MenuItem>
						</Select>
					</Typography>
				</ListItem>
			</List>
			{isDisplay && (
				<HoaTable
					jurisdictionalCost={jurisdictionalCost}
					propertyFormDataStatuses={propertyFormDataStatuses}
				/>
			)}
		</>
	);
};

export default Hoa;
